.manga-details-container {
  display: flex;
}

.date {
  color: rgb(255, 255, 255);
  margin-right: 30%;
  margin-top: 15px;
  font-size: 8px;
  font-weight: 600;
  padding: 8px;
  background-color: rgb(206, 19, 19);
  border-radius: 20px;
}

.tag{
  position: relative;
  font-size: 14px;
  display: inline-block;
  margin-left: 2%;
  padding: 10px;
  background-color: rgba(51, 51, 51, 0.091);
  border-radius: 20px;
}

.header{
  display: flex;
  justify-content: normal;
  align-items: center;
  gap: 13px;
}

.header h1{
  color: #000000;
  margin-left: 5px;
  margin-right: 0;
}

.description{
  padding: 0;
  margin-top: 0;
}


.p2{
  font-size: 16px;
  width: 90%;
  margin-left: 2%;
}

.manga-cover-container {
  flex: 1;
  overflow: hidden;
  position: relative;
}

.manga-cover-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(4px);
}

.manga-details-content {
  flex: 1;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.8);
}

.volume-list {
  list-style: none;
  padding: 5px;
  margin: 0;
  max-height: 276px;
  overflow-y: auto;
  background-color: rgba(51, 51, 51, 0.091);
  border-radius: 6px;
}

.volume-list li {
  margin-bottom: 5px;
}

.volume-link {
  display: block;
  color: #333;
  text-decoration: none;
  font-size: 18px;
  padding: 8px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  height: 26px;
  text-indent: 7px;
}

.volume-link:hover {
  background-color: #f0f0f0;
}



/* Styles for screens under 480px (mobile) */
@media (max-width: 480px) {
  .manga-details-container {
    flex-direction: column;
  }

  .manga-cover-container {
    width: 100%;
    height: 100%;
  }

  .manga-cover-container img {
    width: 100%;
    height: auto;
  }  

  .manga-details-content {
    padding: 10px;
  }
}
